import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'activoPipe'
})
export class ActivoPipe implements PipeTransform {
    transform(value: any, column_name: string): any {
        // Lógica para transformar el valor según tus necesidades
        if (value == 1) {
            return 'Si';
        } else if (value == 0) {
            return 'No';
        } else {
            return value;
        }
    }
}